<template>
  <div>
    <el-card class="box-card">
      <el-row>
<el-button type="primary" @click="handleView" icon="el-icon-plus">添加</el-button>
       <el-button type="primary" 
       @click="multiDel"
       icon="el-icon-delete">批量删除</el-button>
        <b>学生姓名：</b>
<el-input
 v-model="input"
  placeholder="请输入学生姓名"
  style="width: 150px;"
  ></el-input>
<el-button type="primary" @click="searchFun" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" 
        @click="reset"
        icon="el-icon-magic-stick">重置</el-button>

      </el-row>



 <template>
  <el-table
    :data="tableData"
    border
    style="width: 100%"
    max-height="500"
    @selection-change="handleSelectionChange"
    >
     <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
        prop="id"
        label="ID"
        >
      </el-table-column>
      <el-table-column
        prop="clazzName"
        label="所属班级">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名">
      </el-table-column>
      <el-table-column
        prop="sno"
        label="学号">
      </el-table-column>
      <el-table-column
        prop="gender"
        label="性别">
      </el-table-column>
      <el-table-column
        prop="email"
        label="邮箱">
      </el-table-column>
      <el-table-column
        prop="telephone"
        label="电话">
      </el-table-column>
      <el-table-column
        prop="address"
        label="住址">
      </el-table-column>
      <el-table-column
        prop="introducation"
        label="介绍">
      </el-table-column>
      <el-table-column 
      label="操作"
      fixed="right"
      >
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEditView(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        
      </template>
    </el-table-column>
  </el-table>

  <div class="block">
    <el-pagination
     @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="pagesizes"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>
</el-card>

    <StudentDetail ref="StudentDetail" @getDetail="getDetail"/>
  </div>
</template>

<script>
import API from "../../utils/service"
import StudentDetail from '../Details/StudentDetail.vue'
export default {
   components: {
    StudentDetail
  },
   data() {
        return {
          name: '学生管理',
          tableData: [],
          input: '',
          currentPage: 1,
          total: 0,
          pagesize: 5,
          pagesizes: [3,4,5,6],
          ids: [],
          student: {
            address: '碎玉轩',
            clazzName: '四班',
            email: '123@yy.com',
            gender: '女',
            id: 0,
            introduction: '这是一条新数据',
            name: '狗蛋',
            password: '1234',
            portraitPath: '123',
            sno: '123',
            telephone: '123456'
          },
          multipleSelection: [],
          name: '学生管理'
        }
      },
  mounted(){
    this.$bus.$emit('receiveParams',this.name)
    this.pages(1,this.pagesize)
    this.init()
  },
  methods: {
    getDetail(formData,id=0){
      if(id!=0){
        this.student.id = id
      }
      this.student.name = formData.name;
      this.student.gender = formData.gender;
      this.student.clazzName = formData.clazzName;
      this.student.introduction = formData.introduction;
        API({
          url: 'studentController/addOrUpdateStudent',
          data: this.student,
          method: 'post'
        }).then((res)=>{
          this.student.id = 0
          this.init()
          this.handleCurrentChange(this.currentPage)
        })
    },
    handleEditView(index,row) {
      this.$refs.StudentDetail.handleOpen(row.id);
    },


    handleView() {
      this.$refs.StudentDetail.handleOpen();
    },
      handleCurrentChange(val) {
        console.log(val);
        this.pages(val,this.pagesize)
      },
       handleSizeChange(val) {
        this.pagesize = val
        this.pages(this.currentPage,this.pagesize)
      },
    init(){
      API({
        url:'/studentController/getStudents',
        method: 'get',
      }).then((res)=>{
        this.total = res.data.data.length
      })
    },
    pages(val,page) {
      API({
        url:`/studentController/getStudentByOpr/${val}/${page}`,
        method: 'get',
      }).then((response)=>{
        this.tableData = response.data.data.records
        console.log(response);
        })
    },

      handleDelete(index, row) {
         this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let ids = []
        ids.push(row.id)
        this.ids = ids
        API({
        url:'/studentController/delStudentById',
        data: this.ids,
        method: 'delete',
      }).then(res=>{
          this.init()
          this.handleCurrentChange(this.currentPage) 
          this.ids = []
        })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
    searchFun() {
      
      API({
        url:`/studentController/getStudentByOpr/1/${this.total}`,
        params: {
          name: this.input
        },
        method: 'get',
      }).then((res)=>{
        this.input = ''
        this.tableData = res.data.data.records
      })

    },
    reset() {
      this.init()
      this.pages(1,this.pagesize)
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
        this.ids = []
        this.multipleSelection.map(item=>{
          console.log(item.id);
          this.ids.push(item.id)
        })
    },
    multiDel(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        API({
        url:'/studentController/delStudentById',
        data: this.ids,
        method: 'delete',
      }).then(res=>{
          this.init()
          this.handleCurrentChange(this.currentPage) 
          this.ids = []
        })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }

    
  }
}
</script>

<style lang="scss" scoped>
  .el-row{
    margin: 30px 0 50px 0;
  }
  .text {
    font-size: 20px;
  }

  // .item {
  //   padding: 18px 0;
  // }

  .box-card {
    width: 100%;
  }

  .el-card{
    padding: 0px;
  }

  .el-button--primary {
    background-color: #FAB6B6;
    border-color: #FAB6B6;
  }

  .el-button--primary:focus, .el-button--primary:hover {
    background: #e9baba;
    border-color: #f3dfdf;
    color: #FFF;
}

  ::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
	background-color: #f3dfdf;
}



</style>